import { FormEvent, useState } from 'react';
import { Link } from 'react-router-dom';
import { Input, Button, PasswordInput } from "@ya.praktikum/react-developer-burger-ui-components";
import { changePassword } from '../utils/auth';


function ResetPasswordPage () {
    const [code, setCode] = useState('')
    const [pass, setPass] = useState('')

    async function resetPass(evt: FormEvent) {
        evt.preventDefault()
        if(!code || !pass) return
        const formData = { token: code, password: pass }
        const response = await changePassword(formData);
        const { success , error } = response
        if (success) console.log( 'pass changed')
        if (error) console.log('pass change failed')
    }
    
    return (<section className="form-wrap">
        <h3 className="form-title"> </h3>
        <form
            className="form"
            onSubmit={resetPass}
        >
            <div className="form-row">
                <PasswordInput
                    placeholder="   "
                    value={pass}
                    onChange={evt => setPass(evt.target.value)}
                />
            </div>
            <div className="form-row">
            {
                // @ts-ignore
                <Input
                    type="text"
                    value={code}
                    placeholder="   "
                    onChange={evt => setCode(evt.target.value)}
                />
                // @ts-check
            }
            </div>
            <div className="form-buttons">
                <Button
                    htmlType="submit"
                    type="primary"
                >
                    
                </Button>
            </div>
        </form>

        <div className="form-links">
            <span className="form-link-wrap">
                <span className="text text_type_main-default text_color_inactive">
                     ?
                </span>
                <Link to="/login"></Link>
            </span>
        </div>
    </section>)
}

export default ResetPasswordPage;
