import { useState } from "react";
import {
    Input,
    EmailInput,
    PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link } from "react-router-dom";
import { useAuthContext } from "../../services/use-auth";
import style from './profile.module.css';
import { useNavigate } from "react-router";

function ProfilePage () {
    const { user, signOut } = useAuthContext()
    const [password, setPassword] = useState('')
    const navigate = useNavigate();

    async function logoutUser() {
        await signOut()
        navigate('/login')
    }

    return (<section className={style.profileWrap}>
        <div className={style.profile}>
            <div className={style.profileRow}>
                <span className={style.profileItem}></span>
                <Input
                    type="text"
                    value={user?.name}
                    readOnly={true}
                    placeholder=""
                    icon="EditIcon"
                />
            </div>
            <div className={style.profileRow}>
                <Link
                    to="/profile/orders"
                    className={style.profileItem}
                >
                    <span > </span>
                </Link>                
                <EmailInput
                    type="text"
                    value={user?.email}
                    readOnly={true}
                    icon="EditIcon"
                />
            </div>
            <div className={style.profileRow}>
                <span
                    className={`${style.profileItem} _button`}
                    role="button"
                    onClick={logoutUser}
                >
                    
                </span>
                <PasswordInput
                    type="text"
                    value={password}
                    onChange={setPassword}
                />
            </div>
            <div className={style.profileAttention}>
                <span className="text text_type_main-small text_color_inactive">
                            
                </span>
            </div>
        </div>
    </section>)
}

export default ProfilePage;